@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@500&family=Poppins:wght@300&display=swap');
.body{
    /* border: 4px solid red; */
    background-image: url('../../image/bg2.png');
    height: 100vh;
    width: 100vw;
    /* background-attachment: fixed; */
    background-repeat: no-repeat;
   background-position: center;
   background-size: cover;
}
.container{
    border: 1px solid transparent;
    height: 100%;
}

h1{
    margin-top: 20px;
   text-align: center;
   font-family: 'Kanit', sans-serif;
   text-transform: capitalize;
   font-size: 60px;
   color: #2D3E50;
}

h4{
    text-align: center;
    width: 450px;
    margin: auto;
    background-color: #FF7A59;
    font-size: 22px;
    border-bottom-left-radius:15px ;
    border-top-left-radius: 15px;
    color: aliceblue;
}
.box{
   
    margin: 30px auto ;
    width: 50%;
}
.first{
    height: 120px;
    width: 350px;
    background-color: rgb(245, 218, 99);
    margin:8px ;
    display: inline-block;
    font-weight: 600;
    color: #f8f8f8;
    padding: 10px 10px;
    border-radius: 12px 0px 12px 0px;
    box-shadow: 3px 3px 8px 0px rgb(44, 44, 44);

}
.second{
    height: 120px;
    width: 250px;
    background-color:#00BDA5;
    margin:8px ;
    display: inline-block;
    font-weight: 600;
    color: white;
    padding: 10px 10px;
    border-radius: 12px 0px 12px 0px;
    box-shadow: 3px 3px 8px 0px rgb(44, 44, 44);
}



  .card{
    /* border: 2px solid black; */
    /* margin-left: 5px; */
    /* margin: auto; */
    /* margin-top: 2.2%; */
    transition: all 0.4s cubic-bezier(0.175,0.885,0,1);
    background-color: #fff;
    min-width: 230px;
    
    border-radius: 12px;
    box-shadow: 0px 13px 10px -7px rgb(0, 0, 0,0.2);
    /* float: left; */
  }
  .card:hover{
    box-shadow: 20px 30px 18px -8px rgba(0,0,0,0.2);
    transform: scale(1.1,1.1);
  }
  .card_img{
    width: 100%;
    height: 235px;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
  }
  .card_info{
    background-color: #fff;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    padding: 16px 20px 20px 20px;
  }
  .card_category{
    /* font-family: Georgia, 'Times New Roman', Times, serif; */
    text-transform: uppercase;
    font-size: 12px;
    letter-spacing: 1px;
    font-weight: 500;
    color: #868686;
    background-color: #fff;
  }
  
  .card_title1{
    font-size: 20px;
    margin-top: 5px;
    margin-bottom: 10px;
    font-family: 'Poppins', sans-serif;
    background-color: #fff;
    text-transform: capitalize;
  }
  
  button{
    background-color: rgba(255, 255, 255, 0.651);
    padding: 5px 8px;
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #000000;
    outline: none;
    border: 1px solid black;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0px 5px 5px 2px rgba(0, 0, 0, 0.39);
  
  }
  button:hover{
    box-shadow: 0px 8px 8px 3px rgba(0, 0, 0, 0.274);
    transform: scale(1.05,1.05);
  
  }
  
  @media (max-width:768px){
    .card{
      float: none;
      margin: 5%;
      width: 80%;
    }
  }

  .info{
      /* border: 2px solid red; */
      height: 400px;
      width: 1000px;
      margin: auto;
      display: flex;
      
      justify-content:space-around;
      
     
  }

  #nav{
  
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  
