.logbody .glass{
  position: absolute;
  backdrop-filter: blur(20px);
  box-shadow: 0 25px 45px rgba(0,0,0,0.3);
  border: 1px solid rgba(255,255,255,0.5);
  border-right: 1px solid rgba(255,255,255,0.2);
  border-bottom: 1px solid rgba(255,255,255,0.2);
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  animation: animate 10s linear infinite;
  animation-delay: calc(-1s* var(--i));
}
@keyframes animate{
  0%,100%{
    transform:translateY(-40px);
  }
  50%{
    transform:translateY(40px);
  }
}


.logbody .glass:nth-child(1){
  top: -50px;
  right: -60px;
  width: 100px;
  height: 100px;
  --i:0;
}
.logbody .glass:nth-child(2){
  top: 150px;
  left: -100px;
  width: 120px;
  height: 120px;
  z-index: 2;
  --i:1;
}
.logbody .glass:nth-child(3){
  bottom: 50px;
  right: -60px;
  width: 80px;
  height: 80px;
  z-index: 2;
  --i:2;
}
.logbody .glass:nth-child(4){
  bottom: -80px;
  left: 100px;
  width: 50px;
  height: 50px;
  --i:3;
  
}
.logbody .glass:nth-child(5){
  top: -80px;
  left: 140px;
  width: 60px;
  height: 60px;
  --i:4;
  
}
