/* ..................Home card style........... */



.cards{
  
  width: 100vw;
  height: auto;
  /* background: linear-gradient(120deg,rgb(148, 232, 243),rgb(236, 169, 44)); */
}
.card{
  
  margin-left: 3%;
  margin-top: 2.2%;
  transition: all 0.4s cubic-bezier(0.175,0.885,0,1);
  background-color: #fff;
  width: 15.25%;
  
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgb(0, 0, 0,0.2);
  float: left;
}
.card:hover{
  box-shadow: 20px 30px 18px -8px rgba(0,0,0,0.2);
  transform: scale(1.1,1.1);
}
.card_img{
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
.card_info{
  background-color: #fff;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 20px 20px 20px;
}
.card_category{
  /* font-family: Georgia, 'Times New Roman', Times, serif; */
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
}

.card_title{
  font-size: 15px;
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: 'Poppins', sans-serif;
  background-color: #fff;
  text-transform: capitalize;
}

button{
  background-color: rgba(255, 255, 255, 0.651);
  padding: 5px 8px;
  font-family: 'Poppins', sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #000000;
  outline: none;
  border: 1px solid black;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0px 5px 5px 2px rgba(0, 0, 0, 0.39);

}
button:hover{
  box-shadow: 0px 8px 8px 3px rgba(0, 0, 0, 0.274);
  transform: scale(1.05,1.05);

}

@media (max-width:768px){
  .card{
    float: none;
    margin: 5%;
    width: 80%;
  }
}